import React, { Component } from "react";

import styled from "styled-components";

import { Link } from "react-router-dom";

import HOC from "./HOC";

const Main = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin: 50px;
`;

const Sign = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin: 50px; 
  font-size: 1.2em;
  color: #5a5a8c;
`;

class Demo extends Component {
  state = {
    animations: [
      "Bounce",
      "BounceUp",
      "BounceRight",
      "BounceLeft",
      "BounceDown",
      "FadeIn",
      "FadeInUp",
      "FadeInRight",
      "FadeInLeft",
      "FadeInDown",
      "FadeInUpBig",
      "FadeInLeftBig",
      "FadeInRightBig",
      "Flip",
      "FlipX",
      "FlipY",
      "LightOut",
      "LightIn",
      "RotateIn",
      "RotateRight",
      "RotateLeft",
      "RotateUpRight",
      "RotateUpLeft",
      "Flash",
      "RollOut",
      "RollIn",
      "Rubber",
      "Swing",
      "Zoom",
      "Hinge",
      "Pulse",
      "SlideUp",
      "SlideDown",
      "SlideLeft",
      "SlideRight",
      "ExpandUp",
      "Entrance",
      "Hatch"
    ],
    current: ""
  };

  handleChange = event => {
    event.preventDefault();
    this.setState({
      current: event.target.value
    });
  };

  render() {
    const { animations } = this.state;
    const listOfAnimations = animations.map((item, i) => {
      return <option key={i} value={item}>{item}</option>;
    });

    return (
      <div>
        <HOC current={this.state.current} />
        <Main>
          <div className="field">
            <p className="control">
              <span className="select is-small">
                <select onChange={this.handleChange}>
                  {listOfAnimations}
                </select>
              </span>
            </p>
          </div>
        </Main>
        <Link to="/">
          <Sign>
            <i className="fa fa-angle-double-left fa-2x" aria-hidden="true"></i>
          </Sign>
        </Link>
      </div>
    );
  }
}

export default Demo;


