import React, { Component } from "react";

import { css } from "styled-components";
import styled from "styled-components";

import { 
  Bounce,
  BounceUp,
  BounceRight,
  BounceLeft,
  BounceDown,
  FadeIn,
  FadeInUp,
  FadeInRight,
  FadeInLeft,
  FadeInDown,
  FadeInUpBig,
  FadeInLeftBig,
  FadeInRightBig,
  Flip,
  FlipX,
  FlipY,
  LightOut,
  LightIn,
  RotateIn,
  RotateRight,
  RotateLeft,
  RotateUpRight,
  RotateUpLeft,
  Flash,
  RollOut,
  RollIn,
  Rubber,
  Swing,
  Zoom,
  Hinge,
  Pulse,
  SlideUp,
  SlideDown,
  SlideLeft,
  SlideRight,
  ExpandUp,
  Entrance,
  Hatch,
} from "animate-components";

const media = {
  handheld: (...args) => css`
    @media (max-width: 420px) {
      ${css(...args)}
    }
  `
};

const Title = styled.h1`
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin: 50px;
  color: #ff8080;
  font-family: Oxygen, sans-serif;
  font-size: 5.4em;
  ${media.handheld`
    font-size: 1.5em;
  `}
`;

export default class HOC extends Component {
  render() {
    const { current } = this.props;
    return (
      <div>
      	<Title>
      		{
      			current === "Bounce" ? <Bounce>Animate Components</Bounce> : 
      			current === "BounceUp" ? <BounceUp>Animate Components</BounceUp> :
      			current === "BounceDown" ? <BounceDown>Animate Components</BounceDown> :
      			current === "BounceRight" ? <BounceRight>Animate Components</BounceRight> :
      			current === "BounceLeft" ? <BounceLeft>Animate Components</BounceLeft> :
      			current === "FadeIn" ? <FadeIn>Animate Components</FadeIn> :
      			current === "FadeInDown" ? <FadeInDown>Animate Components</FadeInDown> :
      			current === "FadeInLeft" ? <FadeInLeft>Animate Components</FadeInLeft> :
      			current === "FadeInRight" ? <FadeInRight>Animate Components</FadeInRight> :
      			current === "FadeInUp" ? <FadeInUp>Animate Components</FadeInUp> :
      			current === "FadeInRightBig" ? <FadeInRightBig>Animate Components</FadeInRightBig> : 

      			current === "FadeInLeftBig" ? <FadeInLeftBig>Animate Components</FadeInLeftBig> : 
      			current === "FadeInUpBig" ? <FadeInUpBig>Animate Components</FadeInUpBig> : 
      			current === "Flip" ? <Flip>Animate Components</Flip> : 
      			current === "FlipX" ? <FlipX>Animate Components</FlipX> : 
      			current === "FlipY" ? <FlipY>Animate Components</FlipY> : 
      			current === "LightOut" ? <LightOut>Animate Components</LightOut> : 
      			current === "Swing" ? <Swing>Animate Components</Swing> : 
      			current === "LightIn" ? <LightIn>Animate Components</LightIn> : 
      			current === "RotateIn" ? <RotateIn>Animate Components</RotateIn> : 
      			current === "RotateLeft" ? <RotateLeft>Animate Components</RotateLeft> : 
      			current === "RotateRight" ? <RotateRight>Animate Components</RotateRight> : 
      			current === "RotateUpLeft" ? <RotateUpLeft>Animate Components</RotateUpLeft> : 
      			current === "RotateLeft" ? <RotateLeft>Animate Components</RotateLeft> : 
      			current === "RotateUpRight" ? <RotateUpRight>Animate Components</RotateUpRight> : 
      			current === "Flash" ? <Flash>Animate Components</Flash> : 
      			current === "RollIn" ? <RollIn>Animate Components</RollIn> : 
      			current === "RollOut" ? <RollOut>Animate Components</RollOut> : 
      			current === "Rubber" ? <Rubber>Animate Components</Rubber> : 

      			current === "Zoom" ? <Zoom>Animate Components</Zoom> : 
      			current === "Hinge" ? <Hinge>Animate Components</Hinge> : 
      			current === "Pulse" ? <Pulse>Animate Components</Pulse> : 
      			current === "SlideUp" ? <SlideUp>Animate Components</SlideUp> : 
      			current === "SlideDown" ? <SlideDown>Animate Components</SlideDown> : 
      			current === "SlideLeft" ? <SlideLeft>Animate Components</SlideLeft> : 
      			current === "SlideRight" ? <SlideRight>Animate Components</SlideRight> : 
      			current === "ExpandUp" ? <ExpandUp>Animate Components</ExpandUp> : 
      			current === "Entrance" ? <Entrance>Animate Components</Entrance> : 
      			current === "Hatch" ? <Hatch>Animate Components</Hatch> : <FadeInUp>Animate Components</FadeInUp>
      		}
      	</Title>
      </div>
    );
  }
}


