import React, { Component } from "react";

import styled from "styled-components";

const Name = styled.h3`
  font-size: 1.3em;
  color: #29293d;
  font-family: Raleway, sans-serif;
  display: flex;
  margin: 12px;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
`;

const Icons = styled.p`
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: space-around;
  flex-wrap: wrap;
`;

const Item = styled.i`
  padding: 10px;
`;

export default class Footer extends Component {
  render() {
    return (
      <div>
        <Name>Made by Nitin Tulswani</Name>
        <Icons>
          <a href="https://github.com/nitin42/" target="_blank">
            <Item className="fa fa-github fa-2x" aria-hidden="true" />
          </a>
          <a href="https://twitter.com/NTulswani" target="_blank">
            <Item className="fa fa-twitter fa-2x" aria-hidden="true" />
          </a>
          <a href="https://medium.com/@NTulswani" target="_blank">
            <Item className="fa fa-medium fa-2x" aria-hidden="true" />
          </a>
          <a href="https://www.instagram.com/nitintulswani/" target="_blank">
            <Item className="fa fa-instagram fa-2x" aria-hidden="true" />
          </a>
        </Icons>
      </div>
    );
  }
}


