import React from "react";

import styled from "styled-components";
import { css } from "styled-components";

import Typist from "react-typist";

import { ExpandUp } from "animate-components";

import { Link } from "react-router-dom";

import Main from "./components/Main";

import Footer from "./components/Footer";

const media = {
  handheld: (...args) => css`
    @media (max-width: 420px) {
      ${css(...args)}
    }
  `
};

const Heading = styled.h1`
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin: 50px;
  color: #ff8080;
  font-family: Oxygen, sans-serif;
  font-size: 5.4em;
  ${media.handheld`
    font-size: 1.5em;
  `}
`;

const Example = styled.span`
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 8px;
  border-radius: 4x solid;
`;

const Redirect = styled.p`
  font-size: 1.2em;
  color: #5a5a8c;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 10px;
`;

export default class App extends React.Component {
  render() {
    return (
      <div>
        <Heading>
          <Typist
            cursor={{
              blink: true,
              element: "|",
              hideWhenDone: true,
              hideWhenDoneDelay: 10
            }}
          >
            Animate Components
          </Typist>
        </Heading>
        <Link to="demo"><Redirect>View demo</Redirect></Link>
        <Example>
          <ExpandUp><img alt="Example" src="https://i.gyazo.com/9ff96baba5bb47fa598212df6ae3ff5d.png" /></ExpandUp>
        </Example>
        <Main />
        <Footer />
      </div>
    );
  }
}


